import React from "react"
import styled from "styled-components"
import { useStaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"

import Layout from "../../components/layout"
import SEO from "../../components/seo"
import { theme } from "../../theme/GlobalStyles"
import ProjectNav from "../../components/projectnav"
import ProjectText from "../../components/projecttext"

const Img1 = styled(Img)`
    width: 45%;
    display: inline-block;
    margin: 1%;
    @media (max-width: ${theme.mobile}) {
        width: 80vw;
    }
`
const Img2 = styled(Img)`
    width: 45%;  
    display: inline-block;
    margin: 1%;
    @media (max-width: ${theme.mobile}) {
        width: 80vw;
    }  
`
const DigitsPage = () => {
    const data = useStaticQuery(graphql`
        query {
            img1: file(relativePath: { eq: "digits/pca1.png" }) {
                ...image
            }
            img2: file(relativePath: { eq: "digits/pca2.png" }) {
                ...image
            }
        }
    `)

  return (
    <Layout>
      <SEO title="Digits Neural Network" />
      <ProjectNav active="Digits Neural Network">
          <ProjectText
            type="PRINCIPAL COMPONENT ANALYSIS:"
            title="DIGITS NEURAL NETWORK"
            tools="PYTHON"
            body1="I performed PCA on MNIST, a dataset of 60,000 images of handwritten digits, to create a neural network that can recognize and differentiate digits."
            body2="Instead of using pre-existing PCA functions, I implemented it with SVD and successfully performed dimensionality reduction from 784 dimensions to 3 dimensions. This made it possible to visualize the results. To improve the separation between digits, I ran code to train a small neural network to classify 0-9, and extract the neural networkâ€™s interpretation of each image."
            link="Visit Github"
            url="https://github.com/jadechen170/CLA/tree/master/digit-pca">
          </ProjectText>
          <Img1 fluid={data.img1.childImageSharp.fluid} />
          <Img2 fluid={data.img2.childImageSharp.fluid} />
      </ProjectNav>
    </Layout>
  )
}
export default DigitsPage